.container {
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  border-radius: 10px;
  width: 400px;
  box-shadow: 3px 3px 6px rgb(44, 0, 37);

  img {
    height: 200px;
    border-radius: 10px;
  }

  .information {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 80%;
    border-radius: 10px;
    padding: 12px 20px;

    .location {
      text-shadow: 2px 1px 4px rgba(0, 0, 0, 0.438);

      h3 {
        text-align: right;
        font-size: 24px;
      }

      p {
        text-align: right;
        font-size: 20px;
        color: rgba(39, 39, 39, 0.856);
      }
    }

    .stats {
      display: grid;
      width: 60%;
      grid-template-columns: 1fr 1fr;
      text-align: center;
      margin: auto;
    }

    .catContainer {
      display: grid;
      grid-template-columns: 1fr 1fr;
      row-gap: 4px;

      .catStyle {
        font-size: 14px;
        text-shadow: 1px 1px rgba(0, 0, 0, 0.486);
        text-align: right;
      }
    }
  }

  .categoriesHead {
    text-align: center;
    
    span {
      font-size: 12px;
      font-weight: 500;
      text-decoration: underline;
    }
  }
}
